
.klxx-sidebar{position:fixed;width:240px;z-index:999;top:0;left:0;bottom:0;height:100%;background-color:#31373d;transition:background-color 200ms ease;overflow:hidden;color:#d3dbe2;font-size:13px
}
.klxx-sidebar i.icon{font-size:16px
}
.klxx-sidebar .ui.list .list>.item>i.icon,.klxx-sidebar .ui.list>.item>i.icon{padding-right:0
}
.klxx-sidebar .ui.list .list>.item a.header,.klxx-sidebar .ui.list>.item a.header{color:#d3dbe2 !important
}
.klxx-sidebar .ui.list .list>.item a.header:hover a.header,.klxx-sidebar .ui.list>.item:hover a.header,.klxx-sidebar .ui.list .list>.item a.header.actived a.header,.klxx-sidebar .ui.list>.item.actived a.header{color:#FFFFFF !important
}
.klxx-sidebar .ui.list .list>.item a.header:hover a,.klxx-sidebar .ui.list>.item:hover a,.klxx-sidebar .ui.list .list>.item a.header.actived a,.klxx-sidebar .ui.list>.item.actived a{color:#FFFFFF !important
}
.klxx-sidebar .ui.list .list>.item a.header:hover .icon,.klxx-sidebar .ui.list>.item:hover .icon,.klxx-sidebar .ui.list .list>.item a.header.actived .icon,.klxx-sidebar .ui.list>.item.actived .icon{color:#FFFFFF !important
}
.klxx-sidebar a{color:#d3dbe2 !important
}
.klxx-sidebar a:hover{color:#FFFFFF !important;text-decoration:none !important
}
.klxx-sidebar form button{border:0;background:none;outline:none;padding:0;margin:0
}
.klxx-sidebar form button:hover{color:#FFFFFF
}
.klxx-sidebar .section{padding:10px 10px 0 10px
}
.klxx-sidebar .section.top img{max-width:100%
}
.klxx-sidebar .section.menus .item{padding-left:20px;min-height:36px
}
.klxx-sidebar .bottom{position:absolute;left:0;bottom:0;width:100%;padding-bottom:20px
}
.klxx-sidebar .bottom .col-md-6:first-child{padding-right:0
}
.klxx-sidebar .section.top img{max-width:100%
}


