
.j-pagination{margin-top:15px
}
.j-pagination .pagination li{color:#444;display:inline-block;border-radius:2px;text-align:center;vertical-align:top;font-size:1rem;padding:0 10px;height:25px;line-height:25px;cursor:pointer
}
.j-pagination .pagination li:last-child{padding-right:0
}
.j-pagination .pagination li:last-child i{margin-right:0
}
.j-pagination .pagination li.active{color:#fff
}
.j-pagination .pagination li.active{background-color:#21BA45
}
.j-pagination .pagination li.disabled{cursor:default;color:#999
}
.j-pagination .pagination li i{font-size:2rem
}
.j-pagination .pagination li.pages ul li{display:inline-block;float:none
}


